import { createMemoryRouter, RouterProvider } from "react-router-dom";
import "./index.css";
import MainPage from "./pages/mainPage";
import ProductPage from "./pages/productPage";
import MailtypePage from "./pages/mailtypePage";
import ShoppingCart from "./pages/shoppingCart";
import OrderPage from "./pages/order";
import CopyPage from "./pages/copyPage";
import TrackingPage from "./pages/Tracking";
import { ChakraBaseProvider } from "@chakra-ui/react";
import { useStores } from "./store/store_context";
import { useEffect } from "react";
window.GlobalShoppingCart = [];
window.GlobalProductColors = [];
const router = createMemoryRouter([
  {
    path: "/",
    element: <MainPage />,
    errorElement: <MainPage />,
  },
  {
    path: "/product",
    element: <ProductPage />,
    errorElement: <MainPage />,
  },
  {
    path: "/cart",
    element: <ShoppingCart />,
    errorElement: <MainPage />,
  },
  {
    path: "/order",
    element: <OrderPage />,
    errorElement: <MainPage />,
  },
  {
    path: "/tracking",
    element: <TrackingPage />,
    errorElement: <MainPage />,
  },
  {
    path: "/copy",
    element: <CopyPage />,
    errorElement: <MainPage />,
  },
  {
    path: "/home",
    element: <MainPage />,
    errorElement: <MainPage />,
  },
  {
    path: "/mailtype",
    element: <MailtypePage />,
    errorElement: <MainPage />,
  },
]);
const App = () => {
  const { pageStore } = useStores();
  const tg = window.Telegram.WebApp;
  tg.enableClosingConfirmation();
  tg.expand();
  useEffect(() => {
    if (!tg.isExpanded) tg.expand();
  });
  tg.onEvent("viewportChanged", () => tg.expand());
  pageStore.updateFirstName = tg.initDataUnsafe?.user?.first_name;
  pageStore.updateLastName = tg.initDataUnsafe?.user?.last_name;
  pageStore.updateAvatarLink  = window?.avatar_link

  return (
    <ChakraBaseProvider>
      <RouterProvider router={router} />
    </ChakraBaseProvider>
  );
};
export default App;
