import { useState } from "react";
import { useNavigate } from "react-router";
import useWindowDimensions from "../components/GetDimensions";
import { FormControl } from "@chakra-ui/react";
import PhoneInput from "react-phone-input-2";
import "swiper/css";
import "swiper/css/free-mode";
import { Formik, Form, Field } from "formik";
import { useEffect } from "react";
import * as yup from "yup";

function OformitPage() {
  const { width } = useWindowDimensions();
  const [block, setBlock] = useState("cdek");
  const [number, setNumber] = useState("");

  const FormSchema = yup.object().shape({
    last_name: yup.string().required(" "),
    first_name: yup.string().required(" "),
    phone: yup
      .string()
      .matches(/(\+?[\d-\(][\d-\)\s]{6,}\d$)/, "  "),
    adress: yup.string().required(" "),
  });

  const handle_submit = (values) => {
    let last_name = values.last_name;
    let first_name = values.first_name;
    let phone = number;
    let index = values.index;
    let name = values.adress;
    let result = "";
    let a = "";
    for (let i = 0; i < window.GlobalProductColors.length; i++) {
      a += ` ${window.GlobalCartNames[i]}   ${window.GlobalProductColors[i]} \n`;
    }
    result =
      window.GlobalPost === " " ||
      window.GlobalPost === "  "
        ? ` ,   ! 
       :
    : ${first_name} ${last_name}
    : ${phone}
    : ${name}
    : ${window.GlobalPost}
    : ${index}
    : ${window.GlobalSum} 
    : 
    ${a}

        `
        : ` ,   ! 
       :
    : ${first_name} ${last_name}
    : ${phone}
    : ${name}
    : ${window.GlobalPost}
    : ${window.GlobalSum} 
    : 
    ${a}

        `;
    window.GlobalDetails = result;
    console.log(result);
    navigate("/copy");
  };

  const tg = window.Telegram.WebApp;
  const backButton = tg.BackButton;
  const navigate = useNavigate();
  function back_page() {
    navigate("/cart");
  }

  backButton.show();
  backButton.onClick(back_page);

  const initialValues =
    window.GlobalPost === " " ||
    window.GlobalPost === "  "
      ? {
          last_name: "",
          first_name: "",
          phone: "",
          pochta: " ()",
          index: "",
          adress: "",
        }
      : {
          last_name: "",
          first_name: "",
          phone: "",
          pochta: " ()",
          adress: "",
        };
  useEffect(() => {
    if (width <= 410) setBlock("cdek_small");
    else setBlock("cdek");
  });

  return (
    <div id="oformit_main">
      <p id="oformit_header"> </p>
      <div id="oformit_form_div">
        <Formik
          initialValues={initialValues}
          validationSchema={FormSchema}
          onSubmit={handle_submit}
          key="trade-form"
        >
          {(formik) => (
            <Form>
              <Field name="last_name">
                {({ field, form }) => (
                  <FormControl
                    isRequired
                    isInvalid={
                      !!form.values.last_name && !!form.errors.last_name
                    }
                  >
                    <input
                      {...field}
                      placeholder=""
                      className="gray_input"
                      type="text"
                      id="last_name"
                      w="100%"
                    />
                    {form.errors.last_name && (
                      <label style={{ color: "red" }}>
                        {form.errors.last_name}
                      </label>
                    )}
                  </FormControl>
                )}
              </Field>
              <Field name="first_name">
                {({ field, form }) => (
                  <FormControl
                    isRequired
                    isInvalid={
                      !!form.values.first_name && !!form.errors.first_name
                    }
                  >
                    <input
                      {...field}
                      placeholder=""
                      className="gray_input"
                      type="text"
                      id="first_name"
                      w="100%"
                    />
                    {form.errors.first_name && (
                      <label style={{ color: "red" }}>
                        {form.errors.first_name}
                      </label>
                    )}
                  </FormControl>
                )}
              </Field>
              <Field name="phone">
                {({ field, form }) => (
                  <FormControl
                    isRequired
                    isInvalid={!!form.values.phone && !!form.errors.phone}
                  >
                    <PhoneInput
                      country="ru"
                      {...field}
                      inputStyle={{
                        color: "white",
                        backgroundColor: "transparent",
                        width: "90%",
                        height: "40px",
                      }}
                      dropdownStyle={{
                        color: "gray",
                        backgroundColor: "#1a1a1a",
                        colorScheme: "dark",
                      }}
                      buttonStyle={{
                        backgroundColor: "transparent",
                      }}
                      value={number}
                      onChange={(event) => setNumber(event)}
                      placeholder=" "
                      className="gray_input"
                      style={{ padding: "5px 0px" }}
                      type="number"
                      id="phone"
                    />
                    {form.errors.phone && (
                      <label style={{ color: "red" }}>
                        {form.errors.phone}
                      </label>
                    )}
                  </FormControl>
                )}
              </Field>
              {window.GlobalPost === "" ? (
                <p style={{ padding: "0px", marginTop: "15px" }}>
                     
                </p>
              ) : window.GlobalPost === " " ? (
                <p style={{ padding: "0px", marginTop: "15px" }}>
                      
                </p>
              ) : (
                <p style={{ padding: "0px", marginTop: "15px" }}>
                      ,  
                </p>
              )}
              {(window.GlobalPost === "  " ||
                window.GlobalPost === " ") && (
                <Field name="index">
                  {({ field, form }) => (
                    <FormControl
                      isRequired
                      isInvalid={!!form.values.index && !!form.errors.index}
                      maxH="350px"
                    >
                      <input
                        {...field}
                        placeholder=" "
                        className="gray_input"
                        type="text"
                        id="index"
                        w="100%"
                      />
                      {form.errors.index && (
                        <label style={{ color: "red" }}>
                          {form.errors.index}
                        </label>
                      )}
                    </FormControl>
                  )}
                </Field>
              )}

              <Field name="adress">
                {({ field, form }) => (
                  <FormControl
                    isRequired
                    isInvalid={!!form.values.adress && !!form.errors.adress}
                    maxH="350px"
                  >
                    <input
                      {...field}
                      placeholder=" "
                      className="gray_input"
                      type="text"
                      id="adress"
                      w="100%"
                    />
                    {form.errors.adress && (
                      <label style={{ color: "red" }}>
                        {form.errors.adress}
                      </label>
                    )}
                  </FormControl>
                )}
              </Field>
              <div
                style={{
                  width: "100%",
                  display: "flex",
                  justifyContent: "center",
                  marginTop: "10px",
                }}
              >
                <button
                  className="gold_button"
                  type="submit"
                  disabled={
                    !formik.values.last_name ||
                    !formik.values.first_name ||
                    !number ||
                    !formik.values.adress ||
                    !!formik.errors.last_name ||
                    !!formik.errors.first_name ||
                    !!formik.errors.adress
                  }
                  style={{ width: "100%" }}
                >
                   
                </button>
              </div>
            </Form>
          )}
        </Formik>
      </div>
      <div>
        <p id="caution">     </p>
      </div>
    </div>
  );
}
export default OformitPage;
