const Comment = ({
  name = "",
  image_urls = [],
  text = " !",
  date_fetched,
}) => {
  const options = {
    weekday: "long",
    year: "numeric",
    month: "long",
    day: "numeric",
  };
  const date = new Date(date_fetched);
  return (
    <div className="review">
      <div className="review_header">
        <div
          style={{
            display: "flex",
            flexDirection: "row",
            alignItems: "center",
          }}
        >
          {/* <img
              className="review_avatar"
              src={
                elem.attributes.avatar.data != null
                  ? "https://pop.applepodsblack.ru/" +
                    elem.attributes.avatar.data.attributes.url
                  : user_def
              }
            /> */}
          <div>
            <p className="review_name">{name}</p>
            <p className="review_date">
              {date.toLocaleDateString("ru-RU", options)}
            </p>
          </div>
        </div>
      </div>
      <div className="review_images">
        {image_urls.map((elem) => {
          return <img src={elem} />;
        })}
      </div>
      <p className="review_comment">{text}</p>
    </div>
  );
};
export default Comment;
