import { Link } from "react-router-dom";

const ProductCard = ({
  image_url = "https://i.pinimg.com/originals/0d/e8/9f/0de89f5a8bc7f3f2f4682794ecb13318.jpg",
  price = 1500,
  name = "",
  id = 1,
}) => {
  return (
    <div className="card">
      <div className="card_image">
        <img
          src={
            image_url != "" && image_url != null
              ? image_url
              : "https://i.pinimg.com/originals/0d/e8/9f/0de89f5a8bc7f3f2f4682794ecb13318.jpg"
          }
        />
      </div>
      <div className="card_info">
        <div className="card_price_info">
          <p className="card_price">{price} </p>
          <p className="card_description">{name}</p>
        </div>
        <Link
          style={{ width: "100%" }}
          to={"/product"}
          state={{ product_id: id }}
        >
          <button className="gold_button" onClick={() => console.log(id)}>
            
          </button>
        </Link>
      </div>
    </div>
  );
};
export default ProductCard;
