import { makeAutoObservable } from "mobx";

class PageStore {
  first_name = "";
  last_name = "";
  avatar_link =
    "https://i.ytimg.com/vi/l5AXpKPABUk/maxresdefault.jpg?sqp=-oaymwEmCIAKENAF8quKqQMa8AEB-AHUBoAC4AOKAgwIABABGGUgWShOMA8=&rs=AOn4CLAdqZJrD4dW8p6PxeakjqaV_VM3Sg";

  cart_amount = 0;
  sale = 0;
  constructor() {
    makeAutoObservable(this);
  }
  updateFirstName = (new_first) => {
    this.first_name = new_first;
  };
  updateLastName = (new_last) => {
    this.last_name = new_last;
  };
  updateAvatarLink = (new_link) => {
    this.avatar_link = new_link;
  };
  updateCartAmount = (new_amount) => {
    this.cart_amount = new_amount;
  };
}

export default PageStore;
